import Navbar from "./Components/Navbar";
import Home from "./Components/Home/Home";
import Food from "./Components/Menu/Food";
import Drink from "./Components/Menu/Drink";
import Contact from "./Components/Contact";
import Footer from "./Components/Footer";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import "./App.scss";
import foodMenu from "./tekela-dinner-menu-compressed.pdf";

const App = () => {
	return (
		<Router basename={process.env.PUBLIC_URL + "/tekela-restaurant-page"}>
			<Navbar></Navbar>
			<Switch>
				<Route exact path="/" component={Home}></Route>
				<Route exact path="/Home" component={Home}></Route>
				<Route exact path="/Food">
					<Food foodMenu={foodMenu}></Food>
				</Route>
				<Route exact path="/Drinks" component={Drink}></Route>
				<Route exact path="/Contact" component={Contact}></Route>
			</Switch>
			<Footer></Footer>
		</Router>
	);
};

export default App;
